.randomPersonGenerator td {
    font-size: 1rem;
    text-align: left;
}

.randomPersonGenerator td:first-of-type {
    width: 35%;
}

.randomPersonGenerator .card:hover{
    opacity: 1;
    cursor: unset;
}

.randomPersonGenerator .table{
    margin-bottom: 0;
}

.randomPersonGenerator .card {
    max-width: 25rem;
}

/* .randomPersonGenerator Button {
    margin: 1rem auto 0;
} */

.randomPersonGenerator .controls {
    display: block;
    position: relative;
    width: 100%;
    height: 40px;
    margin: 1rem auto 0;
}

.randomPersonGenerator .controls Button {
    display: block;
    position: absolute;
    top: 0;
    height: 40px;
}

.randomPersonGenerator .controls .back {
    left: 0;
}

.randomPersonGenerator .controls .generate {
    right: 0;
}

