
.card {   
    transition: box-shadow, opacity .35s;
    margin: 1rem auto;
    max-width: 20rem;
    color: #000;
    cursor: pointer;
  }
  
  .card:hover {
    box-shadow: 0 0 30px rgba(0,255,0,.9);
    opacity: 0.7;
  }
  
  .card img {
    max-width: 100%;
    max-height: 10rem;
    object-fit: contain;
  }
  